body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content {
  padding: 40px;
  max-width: 900px;
}

.status {
  color: #818080;
}

.profile {
  margin: 0 auto;
}

.photoButton {
  bottom: 30px;
}

.dataEditor {
  margin: 0 auto;
}

.card-image {
  margin: 20px;
  max-width: 300px;
}

.userCard {
  overflow: hidden;
}

.userCard img {
  margin: 20px;
  max-width: 150px;
  max-height: 150px;
}

.btn {
  margin: 20px;
}

.userFillter {
  display: flex;
  flex-direction: column;
}

select {
  display: block;
  margin: 20px auto;
}

@media screen and (max-width: 605px) {
  .listPhotos {
    display: none;
  }
  .profileData {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
